/* stylelint-disable */
.hero {
  background: #130d2c url("../img/new-page/hero-bg-desk.png") no-repeat 50% 0/cover;
  padding-top: 134px;
  position: relative;
}
.hero::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 377px;
  width: 100%;
  background: linear-gradient(180deg, rgba(19, 13, 44, 0) 0%, #130d2c 100%);
}
@media screen and (max-width: 1024px) {
  .hero {
    padding-top: 130px;
  }
}
.hero__container {
  display: flex;
  justify-content: space-between;
  gap: 59px;
}
.hero__info {
  width: 44%;
  position: relative;
  z-index: 1;
  margin-bottom: 130px;
}
.hero__info::before {
  content: "";
  position: absolute;
  bottom: -63px;
  right: -75px;
  width: 227px;
  height: 110px;
  background: url("../img/new-home-page/hero/plane-small.svg") no-repeat 0 0/cover;
}
.hero__title {
  margin-bottom: 30px;
  font-family: "Popins-semb", sans-serif;
  font-size: 54px;
  line-height: 114%;
  background: linear-gradient(254deg, #fff 48.55%, #e6466e 72.99%, #e548ca 97%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.hero__sub-title {
  margin-bottom: 40px;
  color: rgba(255, 255, 255, 0.8);
  font-family: "Popins-reg", sans-serif;
  font-size: 16px;
  line-height: 150%;
}
.hero__play-btn {
  height: 66px;
  width: fit-content;
  padding: 0 42px;
  display: flex;
  gap: 18px;
  align-items: center;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0);
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #e5053d;
  box-shadow: 0px 2.732px 5.465px 0px rgba(255, 98, 65, 0.48);
  color: #fff;
  text-align: center;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.25);
  font-family: "Popins-bold", sans-serif;
  font-size: 20px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  transition: background-color 0.3s ease;
}
.hero__play-btn:hover {
  background-color: #b4012e;
}
.hero__button-arrow {
  width: 27px;
  height: 19px;
  padding-top: 2px;
}
.hero__devices {
  width: 50%;
  position: relative;
  z-index: 1;
}
.hero__devices::before {
  content: "";
  position: absolute;
  top: -36px;
  left: -97px;
  width: 366px;
  height: 134px;
  background: url("../img/new-home-page/hero/plane-big.svg") no-repeat 0 0/cover;
}
.hero__ipad {
  padding: 16px;
  transform: rotate(-13deg);
  margin-left: 20px;
  margin-top: 59px;
  width: 509px;
  height: 390px;
  background: url("../img/new-home-page/hero/ipad.png") no-repeat 0 0/cover;
  position: relative;
}
.hero__ipad::after {
  content: "";
  position: absolute;
  inset: 0;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.8);
  border-radius: 50px;
}
.hero__iphone {
  position: absolute;
  bottom: 0;
  right: -40px;
  background: url("../img/new-home-page/hero/iphone.png") no-repeat 0 0/cover;
  height: 485px;
  width: 276px;
}
.hero__marker {
  width: 320px;
  padding: 20px;
  border: 1px solid 1px solid rgba(153, 153, 153, 0.2);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.084) 0%, rgba(217, 217, 217, 0.042) 100%);
  backdrop-filter: blur(25px);
  border-radius: 10px;
  position: absolute;
  display: flex;
  align-items: center;
  gap: 20px;
}
.hero .marker--ipad {
  bottom: 40px;
  left: 0px;
}
.hero .marker--iphone {
  right: -30px;
  bottom: 100px;
}
.hero .marker__icon {
  width: 60px;
  height: 60px;
  box-shadow: 0px 4px 8px -2px rgba(221, 0, 184, 0.48);
  border-radius: 50%;
}
.hero .marker__title {
  margin-bottom: 8px;
  color: #ffffff;
  font-family: "Popins-semb", sans-serif;
  font-size: 24px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
.hero .marker__description {
  color: rgba(255, 255, 255, 0.8);
  font-family: "Popins-reg", sans-serif;
  font-size: 16px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

@media screen and (max-width: 1400px) {
  .hero__info::before {
    right: 0;
  }
  .hero__devices::before {
    left: -117px;
  }
  .hero__ipad {
    margin-left: -20px;
  }
  .hero__iphone {
    right: 0;
  }
  .hero .marker--ipad {
    left: -40px;
  }
  .hero .marker--iphone {
    right: 0;
  }
}
@media screen and (max-width: 1250px) {
  .hero__title {
    margin-bottom: 25px;
    font-size: 50px;
  }
  .hero__sub-title {
    font-size: 15px;
  }
  .hero__container {
    gap: 0;
  }
  .hero__info {
    width: 47%;
  }
  .hero__devices {
    width: 53%;
    transform: scale(0.9);
    transform-origin: top right;
  }
  .hero__marker {
    width: 290px;
    padding: 18px;
  }
  .hero .marker__icon {
    width: 50px;
    height: 50px;
  }
  .hero .marker__title {
    font-size: 20px;
  }
  .hero .marker__description {
    font-size: 14px;
  }
  .hero .marker--ipad {
    bottom: 20px;
  }
}
@media screen and (max-width: 1120px) {
  .hero__info {
    transform: scale(0.9);
    transform-origin: top left;
  }
}
@media screen and (max-width: 1090px) {
  .hero__container {
    padding-top: 30px;
    flex-direction: column-reverse;
    align-items: center;
    gap: 40px;
  }
  .hero__info {
    width: 100%;
    transform: scale(1);
  }
  .hero__devices {
    width: 80%;
    height: 510px;
    margin-left: 50px;
    transform: scale(1);
  }
  .hero__ipad {
    margin-left: 20px;
  }
}
@media screen and (max-width: 900px) {
  .hero__devices {
    width: 90%;
  }
}
@media screen and (max-width: 800px) {
  .hero__devices {
    width: 100%;
    margin-left: 0;
  }
  .hero__devices::before {
    left: -97px;
  }
  .hero__info {
    margin-top: 70px;
    margin-bottom: 90px;
  }
  .hero__info::before {
    bottom: -45px;
  }
  .hero__ipad {
    margin-left: 40px;
  }
  .hero__marker {
    position: static;
    display: flex;
    width: 46%;
    margin-top: 70px;
  }
  .hero .marker--iphone {
    margin-left: auto;
    margin-top: -85px;
  }
}
@media screen and (max-width: 760px) {
  .hero__devices {
    display: flex;
    height: fit-content;
  }
  .hero__inner {
    display: flex;
    height: fit-content;
    margin: 0 auto;
    width: 100%;
  }
  .hero__ipad {
    margin-bottom: 50px;
    background-size: contain;
    height: fit-content;
  }
  .hero__iphone {
    position: relative;
    background-size: contain;
    margin-left: -90px;
    top: 10px;
    width: 290px;
    height: 63vw;
  }
  .hero__marker {
    position: absolute;
    width: 49%;
  }
  .hero .marker--ipad {
    margin: 0;
    left: 0;
    bottom: -100px;
  }
  .hero .marker--iphone {
    margin: 0;
    bottom: -100px;
  }
}
@media screen and (max-width: 560px) {
  .hero {
    padding-top: 80px;
  }
  .hero__container {
    padding-top: 20px;
  }
}
@media screen and (max-width: 675px) {
  .hero__info {
    margin-top: 110px;
    margin-bottom: 40px;
  }
  .hero__info::before {
    display: none;
  }
  .hero__devices::before {
    width: 187px;
    height: 68px;
    left: -45px;
  }
  .hero__inner {
    width: auto;
  }
  .hero__ipad {
    width: 360px;
    margin-left: 20px;
    margin-bottom: 35px;
    margin-top: 25px;
  }
  .hero__iphone {
    width: 185px;
    height: 370px;
    margin-left: -40px;
  }
  .hero__marker {
    flex-direction: column;
    gap: 10px;
    padding: 18px;
  }
  .hero__title {
    margin-bottom: 26px;
    font-size: 32px;
    line-height: 120%;
  }
  .hero__sub-title {
    margin-bottom: 24px;
    font-size: 12px;
    line-height: 150%;
  }
  .hero__play-btn {
    height: 48px;
    padding: 0 23px;
    gap: 36px;
    font-size: 16px;
    letter-spacing: 0.32px;
  }
  .hero__button-arrow {
    width: 22px;
    height: 15px;
  }
  .hero .marker__icon {
    width: 54px;
    height: 54px;
  }
  .hero .marker__title {
    margin-bottom: 6px;
    font-size: 20px;
    letter-spacing: 0.4px;
    text-align: center;
  }
  .hero .marker__description {
    font-size: 12px;
    letter-spacing: 0.24px;
    text-align: center;
  }
  .hero .marker--ipad, .hero .marker--iphone {
    bottom: -145px;
  }
}
@media screen and (max-width: 555px) {
  .hero__ipad {
    width: 310px;
  }
  .hero__iphone {
    width: 155px;
    height: 300px;
  }
}
@media screen and (max-width: 480px) {
  .hero__ipad {
    margin-left: 10px;
    width: 240px;
  }
  .hero__iphone {
    width: 150px;
    height: 230px;
  }
}
.footer {
  padding: 50px 0 45px 0;
  background: #100C23;
}
.footer__container {
  position: relative;
}
.footer__top {
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 1024px) {
  .footer__top {
    flex-direction: column;
  }
}
.footer__left {
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 1024px) {
  .footer__left {
    flex-direction: row;
    margin-bottom: 30px;
  }
}
.footer__menu {
  display: flex;
  gap: 80px;
}
@media screen and (max-width: 1024px) {
  .footer__menu {
    justify-content: center;
    margin-bottom: 30px;
  }
}
@media screen and (max-width: 700px) {
  .footer__menu {
    gap: 30px;
  }
}
.footer__menu-item {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer__menu-title {
  text-transform: uppercase;
  color: #7e8087;
  font-family: "Popins-semb", sans-serif;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 10px;
}
.footer__menu-link {
  font-family: "Popins-semb", sans-serif;
  font-size: 16px;
  line-height: 29px;
  color: #ffffff;
}
.footer__menu-link:hover {
  color: #ff3a6c;
}
.footer__logo {
  width: 176px;
  height: 60px;
  margin-bottom: 26px;
}
.footer__button {
  margin-bottom: 34px;
  width: 260px;
  height: 66px;
  display: flex;
  gap: 18px;
  justify-content: center;
  align-items: center;
  font-family: "Popins-bold", sans-serif;
  font-size: 20px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #ffffff;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.25);
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #e5053d;
  box-shadow: 0px 2.73239px 5.46479px rgba(255, 98, 65, 0.48);
  border-radius: 8px;
  transition: all 0.3s ease;
}
.footer__button img {
  margin-top: 2px;
}
@media screen and (max-width: 1024px) {
  .footer__button {
    margin-bottom: 0;
    margin-left: auto;
  }
}
.footer__button:hover {
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #b4012e;
}
.footer__social {
  display: flex;
  gap: 12px;
  margin-bottom: 30px;
}
@media screen and (max-width: 1024px) {
  .footer__social {
    justify-content: center;
  }
}
.footer__social-item {
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #e32cc4;
  box-shadow: 0px 4px 8px -2px rgba(227, 44, 196, 0.48);
  cursor: pointer;
  transition: all 0.3s ease;
}
.footer__social-item:hover {
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #ae1494;
}
.footer__info {
  display: flex;
  gap: 10px;
}
.footer__info-logo {
  width: 44px;
  height: 44px;
}
.footer__info-text {
  font-family: "Popins-reg", sans-serif;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.5);
}
.footer__btn-top {
  position: fixed;
  bottom: 40px;
  right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 68px;
  height: 68px;
  transition: all 0.3s ease;
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #e5053d;
  box-shadow: 0px 4px 8px -2px rgba(255, 98, 65, 0.48);
  border-radius: 12px;
  z-index: 99;
}
.footer__btn-top:hover {
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #b4012e;
}
.footer__btn-top span {
  display: inline-block;
  width: 24px;
  height: 34px;
  margin-left: 2px;
  background-color: #fff;
  -webkit-mask: url("../img/new-page/play-btn.svg") no-repeat center;
  -webkit-mask-size: contain;
  mask: url("../img/new-page/play-btn.svg") no-repeat center;
  mask-size: contain;
  transform: rotate(-90deg);
}
.footer__btn-top._hidden {
  opacity: 0;
  visibility: hidden;
}

@media screen and (max-width: 600px) {
  .footer__btn-top {
    z-index: 10;
    bottom: 29px;
    right: 20px;
    width: 50px;
    height: 50px;
  }
  .footer__btn-top span {
    width: 23px;
    height: 30px;
  }
}
@media screen and (max-width: 560px) {
  .footer {
    padding-top: 30px;
    padding-bottom: 15px;
  }
  .footer__logo {
    width: 94px;
    height: 32px;
    margin-bottom: 0;
  }
  .footer__button {
    height: 34px;
    font-size: 14px;
    gap: 14px;
    width: 155px;
  }
  .footer__button-arrow {
    height: 15px;
  }
  .footer__menu {
    flex-direction: column;
    gap: 0;
  }
  .footer__menu-title {
    color: white;
    font-size: 14px;
    padding-block: 12px;
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.3s ease;
    cursor: pointer;
  }
  .footer__menu-title::after {
    content: "";
    display: inline-block;
    width: 13px;
    height: 9px;
    background-color: rgba(255, 255, 255, 0.6);
    -webkit-mask: url("../img/new-page/header-menu-arrow.svg") no-repeat center;
    -webkit-mask-size: contain;
    mask: url("../img/new-page/header-menu-arrow.svg") no-repeat center;
    mask-size: contain;
    margin: 0 0 0 4px;
    transition: all 0.3s ease;
  }
  .footer__menu-title:hover {
    transition: all 0.3s ease;
    color: #e82052;
  }
  .footer__menu-title:hover::after {
    background-color: #e82052;
  }
  .footer__menu-item {
    position: relative;
    gap: 0;
    transition: all 0.3s ease;
  }
  .footer__menu-item::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 1px;
    width: 100%;
    background: -webkit-gradient(linear, right top, left top, from(rgba(227, 44, 196, 0)), to(#e32cc4));
    background: linear-gradient(270deg, rgba(227, 44, 196, 0) 0%, #e32cc4 100%);
    z-index: 1;
  }
  .footer__menu-item:last-child::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 1px;
    width: 100%;
    background: linear-gradient(270deg, rgba(227, 44, 196, 0) 0%, #e32cc4 100%);
    z-index: 1;
  }
  .footer__menu-link {
    height: 0;
    opacity: 0;
    padding-left: 20px;
    transition: height 0.3s ease;
  }
  .footer__menu-link:last-child {
    margin-bottom: 10px;
  }
  .footer__menu-link--active {
    overflow: auto;
    height: 30px;
    opacity: 1;
  }
  .footer__menu-link--active::after {
    content: "";
    display: inline-block;
    width: 14px;
    height: 9px;
    background-color: #e82052;
    -webkit-mask: url("../img/new-page/header-menu-arrow.svg") no-repeat center;
    -webkit-mask-size: contain;
    mask: url("../img/new-page/header-menu-arrow.svg") no-repeat center;
    mask-size: contain;
    margin: 0 0 0 4px;
    transform: rotate(-90deg);
    margin-left: 14px;
  }
}